<template>
  <div class="list">
    <el-table
      :data="predictions"
      :default-sort="{ prop: 'shootingDate', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column fixed prop="shootingDate" label="撮影日" sortable />
      <el-table-column fixed prop="cattleId" label="個体識別番号" />
      <el-table-column fixed="right" width="100">
        <template #default="scope">
          <router-link :to="{name: 'Detail', params: {id: scope.row.id}}">
            <el-button size="mini">
              詳細
            </el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script lang="ts">
import axios from 'axios'
import { defineComponent, inject, reactive } from 'vue'
import { AuthPlugin } from '@/auth'

// todo: 定期箇所をまとめる
interface PredictionResult {
  BMS: number,
  BMS_L2ALLG74: number,
  BMS_L2ALLGL: number,
  BMS_L2G74: number,
  BMS_L2GL: number,
  BMS_L3ALLG74: number,
  BMS_L3ALLGL: number,
  BMS_L3G74: number,
  BMS_L3GL: number,
}

interface Prediction {
  id: number,
  userId: string,
  cattleId: string,
  shootingDate: string,
  input: unknown,
  result: PredictionResult,
}

export default defineComponent({
  name: 'List',
  setup () {
    const backendUrl: string | undefined = inject('BackendUrl')
    if (!backendUrl) {
      throw new Error('BackendUrl is not provided')
    }
    const auth: AuthPlugin | undefined = inject('Auth')
    if (!auth) {
      throw new Error('Auth plugin is not provided')
    }

    const predictions = reactive<Prediction[]>([])

    const getPredictions = async () => {
      const idToken = (await auth.getIdTokenClaims({})).__raw

      const response = await axios.get(`${backendUrl}/v1/prediction`, {
        headers: { Authorization: `Bearer ${idToken}` },
      })
      console.log(response)
      predictions.splice(0, predictions.length, ...response.data)
    }
    getPredictions()

    return {
      predictions,
    }
  },
})
</script>
