<template>
  <div class="detail">
    <h1>{{ prediction?.cattleId }} - {{ prediction?.shootingDate }}</h1>
    <el-row>
      <el-col :span="16" :offset="4">
        <el-table
          :data="exampleToTableData(prediction?.exampleSearch)"
          :show-header="false"
          style="width: 100%"
        >
          <el-table-column prop="name"></el-table-column>
          <el-table-column prop="value"></el-table-column>
        </el-table>
        <el-button @click="showExamples" style="margin: 20px">類似画像</el-button>
        <el-dialog v-model="isShowExamples" width="100%">
          <el-carousel
            height="400px"
            indicator-position="outside"
            arrow="always"
            :autoplay="false"
            :key="carouselReloadKey"
          >
            <el-carousel-item v-for="example in examples" :key="example">
              <el-row>
                <el-col :span="16">
                  <el-image
                    :src="example.image"
                    :preview-src-list="[example.image]"
                    append-to-body
                    hide-on-click-modal
                    fit="scale-down"
                    style="width: 600px; height: 400px"
                  >
                    <template #error>
                      <div class="error-image-slot">
                        <i class="el-icon-picture-outline"></i>
                      </div>
                    </template>
                  </el-image>
                </el-col>
                <el-col :span="8">
                  <el-table
                    height="400px"
                    :data="exampleToTableData(example)"
                    :show-header="false"
                    style="width: 100%"
                  >
                    <el-table-column prop="name"></el-table-column>
                    <el-table-column prop="value"></el-table-column>
                  </el-table>
                </el-col>
              </el-row>
            </el-carousel-item>
          </el-carousel>
        </el-dialog>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <el-card :body-style="{ padding: '0px' }">
          <img
            :src="`${predictionUrl}/${prediction?.input.l2gh}`"
            style="width: 100%; display: block;"
          />
          <div style="padding: 14px">
            <span>L2 高ゲイン</span>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card :body-style="{ padding: '0px' }">
          <img
            :src="`${predictionUrl}/${prediction?.input.l2gl}`"
            style="width: 100%; display: block;"
          />
          <div style="padding: 14px">
            <span>L2 低ゲイン</span>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card :body-style="{ padding: '0px' }">
          <img
            :src="`${predictionUrl}/${prediction?.input.l3gh}`"
            style="width: 100%; display: block;"
          />
          <div style="padding: 14px">
            <span>L3 高ゲイン</span>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card :body-style="{ padding: '0px' }">
          <img
            :src="`${predictionUrl}/${prediction?.input.l3gl}`"
            style="width: 100%; display: block;"
          />
          <div style="padding: 14px">
            <span>L3 低ゲイン</span>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import axios from 'axios'
import { defineComponent, inject, reactive, ref } from 'vue'
import { useRoute } from 'vue-router'
import { AuthPlugin } from '@/auth'

interface PredictionInput {
  l2gh: string,
  l2gl: string,
  l3gh: string,
  l3gl: string,
}

interface PredictionResult {
  BMS: number,
  BMS_L2ALLG74: number,
  BMS_L2ALLGL: number,
  BMS_L2G74: number,
  BMS_L2GL: number,
  BMS_L3ALLG74: number,
  BMS_L3ALLGL: number,
  BMS_L3G74: number,
  BMS_L3GL: number,
}

interface Example {
  bms: number,
  image?: string,
  trapeziusThickness: number,
  trapeziusSubcutaneousFatThickness: number,
  subcutaneousFatThickness: number,
  latissimusThickness: number,
}

interface Prediction {
  id: number,
  userId: string,
  cattleId: string,
  shootingDate: string,
  input: PredictionInput,
  result: PredictionResult,
  exampleSearch?: Example,
}

export default defineComponent({
  name: 'Detail',
  setup () {
    const route = useRoute()
    const auth: AuthPlugin | undefined = inject('Auth')
    if (!auth) {
      throw new Error('Auth plugin is not provided')
    }
    const backendUrl: string | undefined = inject('BackendUrl')
    if (!backendUrl) {
      throw new Error('BackendUrl is not provided')
    }
    const predictionBaseUrl: string | undefined = inject('PredictionsUrl')
    if (!predictionBaseUrl) {
      throw new Error('PredictionsUrl is not provided')
    }

    const prediction = ref<Prediction>()
    const predictionUrl = ref()
    const isShowExamples = ref(false)
    const examples = reactive<Example[]>([])
    const carouselReloadKey = ref(0)

    const getPrediction = async () => {
      const idToken = (await auth.getIdTokenClaims({})).__raw

      const response = await axios.get(`${backendUrl}/v1/prediction/${route.params.id}`, {
        headers: { Authorization: `Bearer ${idToken}` },
      })
      console.log(response)

      prediction.value = response.data

      const userId = prediction.value?.userId
      const cattleId = prediction.value?.cattleId
      const shootingDate = prediction.value?.shootingDate.replaceAll('-', '')
      predictionUrl.value = `${predictionBaseUrl}${userId}/${cattleId}-${shootingDate}`
    }
    getPrediction()

    const exampleToTableData = (ex?: Example) => {
      return [
        { name: 'BMS', value: ex?.bms || '-' },
        { name: '僧帽筋厚さ', value: ex?.trapeziusThickness || '-' },
        { name: '僧帽筋上皮皮下脂肪厚さ', value: ex?.trapeziusSubcutaneousFatThickness || '-' },
        { name: '皮下脂肪厚さ', value: ex?.subcutaneousFatThickness || '-' },
        { name: '広背筋厚さ', value: ex?.latissimusThickness || '-' },
      ]
    }

    const showExamples = async () => {
      console.log('Start similar examples search.')

      const idToken = (await auth.getIdTokenClaims({})).__raw
      const bms = prediction.value?.exampleSearch?.bms
      const tt = prediction.value?.exampleSearch?.trapeziusThickness
      const tsft = prediction.value?.exampleSearch?.trapeziusSubcutaneousFatThickness
      const sft = prediction.value?.exampleSearch?.subcutaneousFatThickness
      const lt = prediction.value?.exampleSearch?.latissimusThickness

      const response = await axios.get(`${backendUrl}/v1/example`, {
        headers: { Authorization: `Bearer ${idToken}` },
        params: { bms, tt, tsft, sft, lt },
      })
      console.log(response)
      examples.splice(0, examples.length, ...response.data)

      // Force re-render carousel
      carouselReloadKey.value++

      isShowExamples.value = true
    }

    return {
      prediction,
      predictionUrl,
      isShowExamples,
      examples,
      carouselReloadKey,
      exampleToTableData,
      showExamples,
    }
  },
})
</script>
